import { __ } from '@wordpress/i18n';
import { useBlockProps, MediaUpload, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl } from '@wordpress/components';

const Edit = ({ attributes, setAttributes }) => {
    const { imageUrl, title, description, learnMoreUrl } = attributes;

    const onChangeImageUrl = (newImageUrl) => {
        setAttributes({ imageUrl: newImageUrl });
    };

    const onChangeTitle = (newTitle) => {
        setAttributes({ title: newTitle });
    };

    const onChangeDescription = (newDescription) => {
        setAttributes({ description: newDescription });
    };

    const onChangeLearnMoreUrl = (newLearnMoreUrl) => {
        setAttributes({ learnMoreUrl: newLearnMoreUrl });
    };

    return (
        <div {...useBlockProps()}>
            <InspectorControls>
                <PanelBody title={__('Destination Details', 'destination-highlight')}>
                    <TextControl
                        label={__('Title', 'destination-highlight')}
                        value={title}
                        onChange={onChangeTitle}
                    />
                    <TextControl
                        label={__('Description', 'destination-highlight')}
                        value={description}
                        onChange={onChangeDescription}
                    />
                    <TextControl
                        label={__('Learn More URL', 'destination-highlight')}
                        value={learnMoreUrl}
                        onChange={onChangeLearnMoreUrl}
                    />
                </PanelBody>
            </InspectorControls>
            <div className="destination-highlight-block">
                <MediaUpload
                    onSelect={onChangeImageUrl}
                    allowedTypes={['image']}
                    value={imageUrl}
                    render={({ open }) => (
                        <img
                            src={imageUrl}
                            alt={title}
                            onClick={open}
                            className={!imageUrl ? 'destination-highlight-placeholder' : ''}
                        />
                    )}
                />
            </div>
        </div>
    );
};

export default Edit;
